@import "../design/main.scss";

.arrow {
  position: fixed;
  bottom: -5px;
  cursor: pointer;
  z-index: 5;
}

.shape {
  fill: #304a63;
  transition: fill 0.2s ease;
  &:hover {
    fill: #6187aa;
  }
}

.name {
  position: absolute;
  bottom: 10px;
  color: #304a63;
  font-family: "PoppinsLightItalic";
  font-size: 11pt;
}
