@import "../design/main.scss";

.mobileWrapper {
  position: absolute;
  top: 10vh;
  height: 600px;
  width: 280px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 40px;
}

.blink {
  position: absolute;
  top: 540px;
  width: 60px;
  left: 41px;
  //   height: 25px;
  background-size: 18px;
  background-repeat: no-repeat;
  animation-name: blinkAnimation;
  animation-duration: 2.5s;
  animation-iteration-count: infinite;
}

.press {
  position: absolute;
  top: 540px;
  width: 30px;
  left: 34px;
  height: 25px;
  background-color: transparent;
  cursor: pointer;
}

@keyframes blinkAnimation {
  0% {
    height: 0;
  }
  10% {
    height: 14px;
  }
  20% {
    height: 0;
  }
  25% {
    height: 15px;
  }
  30% {
    height: 0;
  }
}
