@import "../design/main.scss";

.wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  right: 0;
  background-color: $brightColor;
  outline: 2px $darkColor solid;
  z-index: 2;
  touch-action: none;
}
.svgWrapper {
  position: absolute;
  width: 50%;
  height: 100%;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.imgWrapper {
  position: absolute;
  width: 50%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.image {
  position: relative;
  width: 20px;
  height: 20px;
  background-size: contain;
  background-position: center;
  background-blend-mode: multiply;
  mix-blend-mode: multiply;
  background-repeat: no-repeat;
}

.buttonsWrapper {
  position: absolute;
  top: 40px;
  right: 60px;
  width: 280px;
  height: 40px;
  display: flex;
  justify-content: space-between;
  z-index: 3;
  cursor: pointer;
}

.button {
  position: relative;
  width: 70px;
  height: 40px;
  line-height: 40px;
  background: $darkColor;
  color: $brightColor;
  font-family: "PoppinsMedium";
  font-size: 11pt;
  text-align: center;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
}

.leftButton {
  position: relative;
  width: 70px;
  height: 40px;
  line-height: 40px;
  background: $darkColor;
  color: $brightColor;
  font-family: "PoppinsMedium";
  font-size: 11pt;
  text-align: center;
  border-bottom-left-radius: 20px;
  border-top-left-radius: 20px;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
}

.rightButton {
  position: relative;
  width: 70px;
  height: 40px;
  line-height: 40px;
  background: $darkColor;
  color: $brightColor;
  font-family: "PoppinsMedium";
  font-size: 11pt;
  text-align: center;
  border-bottom-right-radius: 20px;
  border-top-right-radius: 20px;
  opacity: 0.8;
  &:hover {
    opacity: 1;
  }
}

.title {
  position: absolute;
  height: fit-content;
  width: fit-content;
  bottom: 4vh;
  left: 50%;
  transform: translateX(-50%);
  font-family: "PoppinsBold";
  font-size: 22pt;
  color: $darkColor;
}
