@import "../design/main.scss";

.homeWrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  overscroll-behavior: none;
  user-select: none;
}

.header {
  position: absolute;
  width: fit-content;
  height: fit-content;
  left: 50%;
  top: 35%;
  transform: translate(-50%, -50%);
  font-family: "PoppinsMedium";
  font-size: 66pt;
  color: $darkColor;
}

.box {
  position: absolute;
  bottom: 36px;
  left: 7px;
  height: 70px;
  width: 10px;
  background-color: $darkColor;
  animation: IAnimation 2s linear infinite;
}

.dot {
  position: absolute;
  bottom: 111px;
  left: 5px;
  height: 14px;
  width: 14px;
  border-radius: 7px;
  background-color: $darkColor;
  animation: OAnimation 2s linear infinite;
}

.fan {
  position: absolute;
  bottom: 36px;
  left: 470px;
  height: 60px;
  width: 8px;
  background-color: $darkColor;
  animation: fanAnimation 1s linear infinite;
}

.subheader {
  position: absolute;
  width: fit-content;
  height: fit-content;
  left: 50%;
  top: 46%;
  transform: translate(-50%, -50%);
  font-family: "PoppinsMedium";
  font-size: 27pt;
  color: $darkColor;
}

@keyframes IAnimation {
  0% {
    height: 75px;
  }
  25% {
    height: 35px;
  }
  50% {
    height: 15px;
  }
  60% {
    height: 10px;
  }
  90% {
    height: 0%;
  }
  100% {
    height: 75px;
  }
}

@keyframes OAnimation {
  0% {
    bottom: 108px;
  }
  10% {
    bottom: 120px;
  }
  20% {
    bottom: 135px;
  }
  50% {
    bottom: 140px;
  }
  80% {
    bottom: 134px;
  }
  100% {
    bottom: 108px;
  }
}

@keyframes fanAnimation {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
