@import "../design/main.scss";

.wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}

.baseForm {
  position: absolute;
  width: 40vh;
  height: 40vh;
  top: 50%;
  left: 30%;
  transform: translate(-50%, -50%);
  background-color: $darkColor;
  opacity: 1;
  //   transition: opacity ease 2s;
  cursor: pointer;
  border-radius: 10px;
  animation: blinkAnimation 3s linear infinite;
}

.clickMe {
  position: absolute;
  font-family: "PoppinsMedium";
  text-align: center;
  font-size: 12pt;
  color: $paleColor;
  cursor: pointer;
  top: 40%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
}

.codeWrapper {
  position: absolute;
  font-family: "PoppinsLightItalic";
  text-align: left;
  font-size: 16pt;
  background-color: $paleColor;
  color: $darkColor;
  padding: 12px;
  left: 50%;
  border-color: $markerColor;
  border-style: solid;
  border-width: 3px;
}

@keyframes blinkAnimation {
  0% {
    transform: translate(-50%, -50%) rotate(0);
  }
  10% {
    transform: translate(-50%, -50%) rotate(2deg);
  }
  20% {
    transform: translate(-50%, -50%) rotate(0);
  }
  30% {
    transform: translate(-50%, -50%) rotate(2deg);
  }
  40% {
    transform: translate(-50%, -50%) rotate(0);
  }
  100% {
    transform: translate(-50%, -50%) rotate(0);
  }
}
